.try-invoice{
    background-image: url('/src/assets/try-invoice-bg.png');
    background-size: cover;
    display: flex;
    justify-content: center;
}
.try-invoice .try-invoice-heading{
    font-size:50px;
    font-weight: bold;
    color:#2B2B2B;
    text-align: left;
    margin-bottom: 10px;
    letter-spacing: -1px;
}
.try-invoice .try-invoice-paragraph{
    font-size:14px;
    font-weight: 300;
    color:#2b2b2b;
    text-align: left;
}
@media(max-width:420px){
    .try-invoice{
        display:flex;
        align-items: flex-end;
        height:50vh;
        background-image: url('/src/assets/try-invoice-bg-m.png');
    }
    .try-invoice .try-invoice-heading{
        font-size:28px;
    }
    .try-invoice .try-invoice-paragraph{
        font-size:12px;
    }
}