@import url("https://fonts.googleapis.com/css?family=Poppins:300,400,600,700");
body {
  margin: 0;
  background-color:white;
  font-family: "Poppins", sans-serif !important;
}
h1, h2, h3, h4, h5, h6, p{
  font-family: "Poppins", sans-serif;
  color:#2B2B2B;
  margin:0;
}
li{
  list-style: none;
}
img{
  height:auto;
  max-width: 100%;
}
a{
  text-decoration: none !important;
}
a:hover{
  text-decoration: none;
}
.section-padding-90{
  padding-top:90px;
  padding-bottom: 90px;
}
.section-padding-90-0{
  padding-top:90px;
  padding-bottom:0px;
}
.section-padding-0-90{
  padding-top: 0px;
  padding-bottom: 90px;
}
.bg-img{
  background-size: cover;
  background-repeat: no-repeat;
  background-position:center center;
}

/* // Flex CSS // */
.flex-center{
  display:flex;
  justify-content: center;
  align-items: center;
}
.flex-start{
  display:flex;
  justify-content: flex-start;
}
.flex-center-row{
  display:flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}
.flex-center-column{
  display:flex;
  justify-content: center;
  flex-direction: column;
}
.flex-start-column{
  display:flex;
  justify-content: flex-start;
  flex-direction: column;
}
.flex-start-baseline{
  display:flex;
  justify-content: flex-start;
  align-items: baseline;
  flex-direction:column
}
.flex-start-center{
  display:flex;
  justify-content: flex-start;
  align-items: center;
}
.flex-end-center{
  display: flex;
  justify-content: center;
  align-items: flex-end;
}
.flex-end-column{
  display: flex;
  align-items: end;
  flex-direction: column;
}
.flex-space-between{
  display: flex;
  justify-content: space-between;
}

/* // Margins // */
.mt-20{
  margin-top:20px;
}
.ml-15{
  margin-left:15px;
}
.mt-15{
  margin-top:15px;
}
.mb-15{
  margin-bottom: 15px;
}
.mr-15{
  margin-right: 15px;
}
.mt-50{
  margin-top: 50px;
}
.mt-40{
  margin-top:40px;
}
.mb-50{
  margin-bottom:50px;
}
.h-60{
  height:60vh;
}
.w-100{
  width:100%;
}
.m-20-0{
  margin:20px 0px;
}
/* .mt-50-m{
  margin-top:-10vh !important;
} */





.green-color{
  color:#27C492;
}
.h-hero-section{
  background-image: url('./assets/hero-bg.svg');
  height:70vh;
  width:100%;
  background-position: center center;
}
.store-btn{
  background-color: #2B2B2B;
  border-radius:35px;
  padding:10px 20px;
  color: white;
  font-weight: 400 !important;
  text-transform: none !important;
  width: 220px;
  /* width:max-content; */
  text-align: center;
  transition: 0.3s;
  transition-duration: 0.3s;
  border: 1px solid #2B2B2B;
  -webkit-transition-duration: 500ms;
  transition-duration: 500ms;
  text-decoration: none;
  margin-right: 20px;
  margin-bottom:20px;
}
.store-btn .get-it-now{
  color: inherit;
  font-size: 12px;
  margin-bottom: 0px;
  text-align: left;
  font-weight: 300;
}
.store-btn .store-name{
  font-size: 15px;
  margin-bottom: 0px;
  color: inherit;
  text-align: left;
  font-weight: 400;
}
.store-btn:hover{
  background-color:#fff;
  color:#2B2B2B !important;
  cursor: pointer;
}
/* // SVG Class // */
.svg-class{
  fill:#fff;
}
.store-btn:hover .svg-class{
  fill: #2b2b2b;
}
.learn-more-btn{
  background-color: #fff;
  border-radius:35px;
  padding:10px 20px;
  color: #8B7DD5;
  font-weight: 400 !important;
  text-transform: none !important;
  width: 220px;
  text-align: center;
  transition: 0.3s;
  transition-duration: 0.3s;
  border: 2px solid #8B7DD5;
  -webkit-transition-duration: 500ms;
  transition-duration: 500ms;
  text-decoration: none;
  /* margin-right: 20px; */
  font-size:18px;
  font-weight: 500;
}
.learn-more-btn:hover{
  background-color:#8B7DD5;
  color:#fff !important;
  cursor: pointer;
}


@media(max-width:420px){
  .h-hero-section{
    height:100vh;
    background-image: url('./assets/hero-bg-m.svg');
  }
  .store-btn{
    padding:10px;
  }
  .store-btn .get-it-now{
    font-size:10px;
  }
  .store-btn .store-name{
    font-size:12px;
  }
  .svg-icon{
    width:auto;
    height:60%;
  }
  .checkmark-img{
    width:auto;
    height:50%;
  }
  .h-60{
    height:auto;
  }
  .learn-more-btn{
    font-size:14px;
    width:160px;
    padding:10px;
    font-weight: 300 !important;
    border:1px solid #8B7DD5;
  }
  .flex-start-res{
    display: flex;
    justify-content: flex-start;
  }
  .section-padding-90{
    padding-top:60px;
    padding-bottom: 60px;
  }
  .section-padding-90-0{
    padding-top:60px;
  }
  .mb-50{
    margin-bottom:50px;
  }
  .mt-50{
    margin-top:30px;
  }
  .flex-end-column{
    align-items: flex-start;
    padding-left: 20px;
  }
}