.review{
    background-color: #F6F7FE;
}
.review .review-box{
    background-color: #343B51;
    /* display:flex;
    justify-content: center;
    align-items: center;
    flex-direction: column; */
    padding:30px;
    border-radius: 10px;
}
.review .review-box .review-img{
    margin-top: -20vh;
    height:150px;
    width: 150px;
    position: absolute;
}
.review .review-box .review-box-div{
    padding:0px 50px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}
.review .review-box .review-box-div .review-box-heading{
    font-size: 18px;
    text-align: center;
    color: white;
    font-weight: 300;
    width:80%;
    margin-bottom:30px;
}
@media(max-width:420px){
    .review .review-box{
        padding:15px;
    }
    .review .review-box .review-box-div .review-box-heading{
        width:100%;
        font-size:14px;
    }
}