/* // Flex CSS // */
.flex-center{
  display:-webkit-flex;
  display:flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
.flex-start{
  display:-webkit-flex;
  display:flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
}
.flex-center-row{
  display:-webkit-flex;
  display:flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: row;
          flex-direction: row;
}
.flex-center-column{
  display:-webkit-flex;
  display:flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
}
.flex-start-column{
  display:-webkit-flex;
  display:flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-flex-direction: column;
          flex-direction: column;
}
.flex-start-baseline{
  display:-webkit-flex;
  display:flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: baseline;
          align-items: baseline;
  -webkit-flex-direction:column;
          flex-direction:column
}
.flex-start-center{
  display:-webkit-flex;
  display:flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: center;
          align-items: center;
}
.flex-end-center{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: flex-end;
          align-items: flex-end;
}
.flex-end-column{
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: end;
          align-items: end;
  -webkit-flex-direction: column;
          flex-direction: column;
}
.flex-space-between{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

