.store-btn{
  background-color: #2B2B2B;
  border-radius:35px;
  padding:10px 20px;
  color: white;
  font-weight: 400 !important;
  text-transform: none !important;
  width: 220px;
  /* width:max-content; */
  text-align: center;
  transition: 0.3s;
  transition-duration: 0.3s;
  border: 1px solid #2B2B2B;
  transition-duration: 500ms;
  text-decoration: none;
  margin-right: 20px;
  margin-bottom:20px;
}
.store-btn .get-it-now{
  color: inherit;
  font-size: 12px;
  margin-bottom: 0px;
  text-align: left;
  font-weight: 300;
}
.store-btn .store-name{
  font-size: 15px;
  margin-bottom: 0px;
  color: inherit;
  text-align: left;
  font-weight: 400;
}
.store-btn:hover{
  background-color:#fff;
  color:#2B2B2B !important;
  cursor: pointer;
}
/* // SVG Class // */
.svg-class{
  fill:#fff;
}
.store-btn:hover .svg-class{
  fill: #2b2b2b;
}
.learn-more-btn{
  background-color: #fff;
  border-radius:35px;
  padding:10px 20px;
  color: #8B7DD5;
  font-weight: 400 !important;
  text-transform: none !important;
  width: 220px;
  text-align: center;
  transition: 0.3s;
  transition-duration: 0.3s;
  border: 2px solid #8B7DD5;
  transition-duration: 500ms;
  text-decoration: none;
  /* margin-right: 20px; */
  font-size:18px;
  font-weight: 500;
}
.learn-more-btn:hover{
  background-color:#8B7DD5;
  color:#fff !important;
  cursor: pointer;
}


