.toolbar{
    position:-webkit-sticky;
    position:sticky;
    width:100%;
    height:70px;
    top:0px;
    left:0px;
    z-index:899;
    background-color: #fff;
    box-shadow: 0px 0px 10px 2px rgba(0,0,0,0.15);
  }
  .toolbar .toolbar-navigation{
    display:-webkit-flex;
