.features .features-img{
    height: 60vh;
}
.features .feature-div .feature-heading{
    margin-bottom:20px;
    font-size:35px;
    color:#2B2B2B;
    text-align: left;
    font-weight: 600;
}
.features .feature-div .feature-paragraph{
    width:80%;
    margin-bottom:20px;
    font-weight: 300;
}
@media(max-width:420px){
    .features .features-img{
        height:auto;
        margin-bottom:30px;
    }
    .features .feature-div .feature-heading{
        font-size: 25px;
    }
    .features .feature-div .feature-paragraph{
        font-size: 14px;
        width:100%;
        margin-bottom:30px;
    }
}