.banner-part{
    margin-top:-1vh;
}
.banner-part .banner-heading{
    font-size:45px;
    font-weight: bold;
    color:#2B2B2B;
    margin-bottom:20px;
}
.banner-part .banner-description{
    font-size:16px;
    font-weight:300;
    color:#2B2B2B;
    margin-bottom:25px;
}
.privacy-policy{
    font-size:14px;
    font-weight:300;
    color:#2b2b2b;
    margin-bottom:0px
}
.checkmark{
    display: flex;
    justify-content: baseline;
    margin-right:30px;
}
.checkmark .checkmark-text{
    color: #2b2b2b;
    text-align: left;
    font-weight:300;
    margin-bottom:0;
    line-height: 1.6;
}
@media(max-width:420px){
    .banner-part .banner-heading{
      font-size:25px;
      margin-top:20px;
    }
    .banner-part .banner-description, .privacy-policy{
        font-size:14px;
    }
    .checkmark{
        margin-right:15px;
    }
    .checkmark .checkmark-text{
        font-size:12px;
    }
}
