.footer{
    background-color:#343B51;
}
.footer .list-items{
    margin-bottom:30px;
}
.footer .logo{
    font-size:40px;
    font-weight: 300;
    color:white;
    text-align: left;
}
.footer .list-items li{
    color:white;
    font-size:14px;
    margin-bottom:15px;
    border-bottom:3px solid #343B51;
    transition: 0.3s;
    cursor: pointer;
}
.footer .list-items li:hover{
    border-bottom: 3px solid white;
}
.footer .footer-line{
    width:100%;
    height:1.5px;
    background-color: #fff;
    border-radius:1px;
}
.footer .copyright{
    margin-bottom:0px;
    color:white;
    font-size:12px;
    font-weight: 300;
}
.footer .footer-logo{
    width: 15%;
    height: auto;
}
@media(max-width:420px){
    .footer .footer-logo{
        padding-left:15px;
        margin-bottom:30px;
    }
    .footer .list-items li{
        font-size:12px;
    }
}