.side-drawer{
    height:100%;
    background-color: #fff;
    position: fixed;
    top:0;
    left:0;
    width:70%;
    max-width:400px;
    z-index:1000;
    transform: translateX(-100%);
    transition: transform 0.3s ease-out;
}
.side-drawer.open{
    transform: translateX(0px);
    box-shadow: 2px 0px 20px rgba(0,0,0,0.43);
}
.side-drawer ul{
    display:flex;
    flex-direction: column;
    list-style: none;
    justify-content: center;
    height: 100%;
}
.side-drawer li{
    margin:10px 0px;
}
.side-drawer a{
    color:#2B2B2B;
    border:3px solid #fff;
    text-decoration: none;
}
.side-drawer a:hover, .side-drawer a:focus{
    border-bottom:3px solid #27c492; 
}