.plans .plan{
    border-radius:10px;
    background-color: #F7F8FE;
    padding:35px 30px;
    display:flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
}
.plans .plan .intro{
    font-size:14px;
    text-align: left;
    margin-bottom:15px;
    font-weight: 300;
}
.plans .plan .pricing{
    font-size:30px;
    font-weight: 400;
    text-align: left;
    margin-right:5px;
    margin-bottom:0px;
}
.plans .plan .pricing-month{
    opacity:0.5;
    font-size:10px;
    text-align: left;
}
.plans .plan .pricing-description{
    font-size:12px;
    font-weight: 300;
    text-align: left;
}
.plans .plan .pricing-description-small{
    font-size:12px;
    font-weight: 300;
    text-align: left;
    margin-bottom: 0;
}
.plans .plan .img-plan{
    height:18px;
    width:18px;
    margin-right: 10px;
}
.plans .plan .btn-plan{
    background-color: white;
    border:1px solid #8B7DD5;
    border-radius:35px;
    padding:15px 0px;
    width:90%;
    outline:none;
    transition: 0.3s;
    font-size:12px;
    text-align: center;
    font-weight: 300;
    margin-top:30px;
}
.plans .plan .btn-plan:hover{
    background-color: #8B7DD5;
    color:white;
}
.plans .special{
    background-color:#583DE6 !important;
}
.plans .special .intro, .special .pricing-description, .special .pricing-description-small, .special .pricing-month, .special .pricing{
    color:white;
}
.plans .special .btn-plan:hover{
    background-color: #FFC6E9;
}
@media(max-width:420px){
    .plans .plan{
        padding:30px;
        margin-top:20px;
        margin-left:20px;
        margin-right:20px;
    }
}