.toolbar{
    position:sticky;
    width:100%;
    height:70px;
    top:0px;
    left:0px;
    z-index:899;
    background-color: #fff;
    box-shadow: 0px 0px 10px 2px rgba(0,0,0,0.15);
  }
  .toolbar .toolbar-navigation{
    display:flex;
    align-items:center;
    height:100%;
    padding:10px 25px;
  }
  .toolbar .toolbar-navigation-items ul{
    list-style: none;
    margin:0;
    padding:0;
    display:flex;
  }
  .toolbar .toolbar-navigation-items li{
    padding: 5px 15px;
  }
  .toolbar .toolbar-logo{
    margin-left:10px;
    width:30%;
    height:auto;
  }
  .toolbar .toolbar-logo a{
    text-decoration: none;
  }
  .toolbar .toolbar-navigation-items a{
    text-decoration: none;
    font-size:14px;
    color:#2B2B2B;
    border-bottom:3px solid #fff; 
    font-weight: 400;
    transition: 0.3s;
  }
  .toolbar .toolbar-navigation-items a:hover,
  .toolbar .toolbar-navigation-items a:active{
    border-bottom:3px solid #27c492; 
  }

  .spacer{
    flex-grow:1;
  }
  @media(max-width:760px){
    .toolbar-navigation-items{
      display:none;
    }
  }
  @media(min-width:761px){
    .toggle-button{
      display:none;
    }
  }
  @media(max-width:420px){
    .toolbar{
      height:60px;
    }
    .toolbar .toolbar-logo{
      width:25px;
      height:auto;
    }
    .toggle-button img{
      width:25px;
      height:auto;
    }
  }