.start{
    background-color: #F7F8FE;
}
.start .start-now-heading{
    font-size:50px;
    font-weight: bold;
    letter-spacing: -2px;
    margin-bottom:50px;
    text-align: left;
}
.start .start-now-paragraph{
    font-size:16px;
    font-weight: 400;
    margin-bottom: 0px;
    text-align: left;
}
.start .start-now-img{
    width:100%;
    height:auto;
    max-width: 100%;
}
@media(max-width:420px){
    .start .start-now-heading{
        font-size:28px;
        margin-bottom: 30px;
        letter-spacing: -1px;
    }
    .start .start-now-paragraph{
        font-size:14px;
        font-weight: 300;
    }
}