.benefits .benefits-block{
    background-color: white;
    border-radius: 5px;
    /* box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.2);
    -webkit-box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.2);
    -moz-box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.2); */
    padding:40px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border:1px solid #fff;
    transition: 0.3s;
}
.benefits .benefits-block .benefit-img{
    height:85px;
    width:85px;
    margin-bottom: 15px;
}
.benefits .benefits-block .benefits-heading{
    font-size:25px;
    text-align: center;
    color:#2B2B2B;
    font-weight: 600;
    margin-bottom: 15px;
}
.benefits .benefits-block .benefits-paragraph{
    font-size:14px;
    font-weight: 300;
    text-align: center !important;
    color:#2b2b2b;
}
.benefits .benefits-block:hover{
    border:1px solid #8B7DD5;
    cursor: pointer;
}
@media(max-width:420px){
    .benefits .benefits-block{
        padding:20px;
        margin-left:30px;
        margin-right:30px;
        border-radius:10px;
        box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.2);
    -webkit-box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.2);
    -moz-box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.2);
    }
    .benefits .benefits-block .benefits-heading{
        font-size:18px;
    }
    .benefits .benefits-block .benefits-paragraph{
        font-size:12px;
        width:80%;
    }
}